<template>
    <div class="container ml-0">
        <div class="row" v-if="requestStatus === 'FAILURE'">
            <div class="col-md-12 col-lg-6">
                <div class="alert alert-danger" role="alert">
                    {{ errorMessage || defaultErrorMessage }}
                </div>
            </div>
        </div>
        <div class="row" v-if="requestStatus === 'SUCCESS'">
            <div class="col-md-12 col-lg-6">
                <div class="alert alert-success" role="alert">
                    {{ defaultSuccessMessage }}
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col-md-8 col-lg-4">
                <div class="input-group mb-3">
                    <span class="input-group-text">@</span>
                    <input type="text" class="form-control" placeholder="your@email.com" v-model="emailAddress">
                </div>
            </div>
            <div class="col-md-4 col-lg-2">
                <button v-if="requestStatus === 'IN_PROGRESS'" class="btn btn-success w-100" type="button" disabled>Subscribing...</button>
                <button v-else class="btn btn-success w-100" type="button" @click="trySubscribe(emailAddress)">Subscribe
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: "SubscribeComponent",
    data() {
        return {
            subscribeUrl: process.env.VUE_APP_API_GATEWAY_URL + '/email',
            defaultErrorMessage: 'Something went wrong during subscription. Please try again later.',
            defaultSuccessMessage: 'Subscription request sent successfully.',
            emailRegex: /^\S+@\S+$/,
            requestStatus: 'NOT_STARTED',
            emailAddress: undefined,
            errorMessage: undefined,
        }
    },

    methods: {
        trySubscribe(emailAddress) {
            this.clearDataAndMessages();

            if (this.emailIsValid(emailAddress)) {
                this.sendSubscriptionRequest(emailAddress);
            } else {
                this.requestStatus = 'FAILURE';
                this.errorMessage = `Email ${emailAddress || ""} doesn't seem valid.`;
                this.emailAddress = emailAddress;
            }
        },

        sendSubscriptionRequest(emailAddress) {
            this.requestStatus = 'IN_PROGRESS';

            let model = {
                customerEmailAddress: emailAddress,
                customerName: 'PMN SUBSCRIPTION',
                messageBody: `PMN SUBSCRIPTION: ${emailAddress}`
            };

            axios.post(this.subscribeUrl, model)
                .then(response => {
                    if (response && response.status && response.status === 200) {
                        this.clearDataAndMessages();
                        this.requestStatus = 'SUCCESS';
                    } else {
                        this.requestStatus = 'FAILURE';
                    }
                })
                .catch(() => {
                    this.requestStatus = 'FAILURE';
                })
        },

        emailIsValid(email) {
            return this.emailRegex.test(email) && email.indexOf('+') < 0;
        },

        clearDataAndMessages() {
            this.errorMessage = undefined;
            this.requestStatus = 'NOT_STARTED';
            this.emailAddress = undefined;
        }
    }
};
</script>

